@font-face {
  font-family: GothamHTF;
  src: url('./GothamHTF-Book.woff');
  font-weight: 400;
  font-style: normal
}

* {
  margin: 0;
  padding: 0;
  font-family: GothamHTF;
  font-size: 15px;
  line-height: normal;
  font-weight: 400;
  font-style: normal;
  font-stretch: normal;
  letter-spacing: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale
}

.loader {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  z-index: 9;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}

.loader>div {
  background-color: #333;
  height: 25%;
  width: 6px;
  margin-left: 5px;
  -webkit-animation: sk-stretchdelay 1.2s infinite ease-in-out;
  animation: sk-stretchdelay 1.2s infinite ease-in-out;
  border-radius: 15px;
}

.loader .rect2 {
  -webkit-animation-delay: -1.1s;
  animation-delay: -1.1s;
}

.loader .rect3 {
  -webkit-animation-delay: -1.0s;
  animation-delay: -1.0s;
}

.loader .rect4 {
  -webkit-animation-delay: -0.9s;
  animation-delay: -0.9s;
}

.loader .rect5 {
  -webkit-animation-delay: -0.8s;
  animation-delay: -0.8s;
}

@keyframes sk-stretchdelay {

  0%,
  40%,
  100% {
    transform: scaleY(0.4);
    -webkit-transform: scaleY(0.4);
  }

  20% {
    transform: scaleY(1.0);
    -webkit-transform: scaleY(1.0);
  }
}

.corona-graph>div {
  margin: 20px 0;
  padding: 10px;
  box-shadow: 0 0 5px 3px hsla(0, 0%, 85%, .5);
  max-width: 96%;
  overflow: hidden;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.total-cases,
.corona-graph,
.corona-items-list {
  animation-duration: .45s;
  animation-fill-mode: both;
  animation-name: fadeInUp;
}

@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  padding: 10px 45px 10px 0;
  z-index: 1;
  background: #fff;
  box-shadow: 0 0 5px 3px hsla(0, 0%, 85%, .5);
  display: flex;
  align-items: center;
  justify-content: space-between;
}

header img {
  height: 50px;
}

header * {
  float: right;
  color: #000;
  font-weight: bold;
  margin-left: 20px;
}

h1 {
  font-size: 35px;
  margin: 0;
  font-weight: normal;
  margin-bottom: 50px;
  text-align: center;
}

.corona-wrapper {
  max-width: 1280px;
  margin: 100px auto 30px;
  width: 95%;
}

.corona-wrapper .total-cases {
  display: flex;
  position: relative
}

.corona-wrapper .total-cases .latest-update {
  position: absolute;
  top: -50px;
  right: 0;
  color: red;
  width: auto;
}

.corona-wrapper .total-cases>div {
  display: flex;
  align-items: center;
  width: 50%;
  justify-content: space-around;
}

.corona-wrapper .total-cases p {
  border-radius: 10px;
  padding: 15px 15px 35px;
  color: #fff;
  font-weight: bolder;
  font-size: 25px;
  text-align: center;
  min-width: 11.5vw;
  position: relative;
  box-shadow: 0 0 5px 3px hsla(0, 0%, 85%, .5)
}

.corona-wrapper .total-cases p.confirmed {
  background-color: #ff8c00
}

.corona-cases-statename {
  cursor: pointer;
}

.corona-wrapper .corona-item .corona-cases-citywise .corona-cases-confirmed-last,
.corona-wrapper .total-cases p.confirmed sub {
  color: #ff8c00;
  font-weight: bold;
}

.corona-wrapper .total-cases p.active {
  background-color: #dc143c;
  padding: 25px 15px;
}

.corona-wrapper .total-cases p.deceased {
  background-color: #a1a1a1
}

.corona-wrapper .total-cases p.deceased sub {
  color: #a1a1a1
}

.corona-wrapper .total-cases p.recovered {
  background-color: #28a745
}

.corona-wrapper .total-cases p span {
  display: block;
  font-size: 20px;
  margin-bottom: 10px
}

.corona-wrapper .total-cases p sub {
  background: #fff;
  color: #28a745;
  position: absolute;
  bottom: 10px;
  right: 10px;
  border-radius: 5px;
  padding: 3px 8px;
  font-size: 12px;
  font-weight: 700
}

.corona-wrapper .corona-item .corona-cases-citywise .corona-cases-confirmed-last:before,
.corona-wrapper .total-cases p sub:before {
  content: "";
  border-color: inherit;
  border-width: 0 2px 2px 0;
  border-style: solid;
  display: inline-block;
  padding: 2px;
  -ms-transform: rotate(-135deg);
  transform: rotate(-135deg);
  margin-right: 5px
}

.corona-wrapper .total-cases p+span {
  font-size: 25px;
  font-weight: bolder
}

.corona-wrapper .corona-item {
  position: relative
}

.corona-wrapper .corona-items-heading {
  position: sticky;
  top: 0;
  z-index: 1;
  box-shadow: 0 3px 4px 1px hsla(0, 0%, 85%, .5);
  border: 0 !important
}

.corona-wrapper .corona-items-heading .corona-cases-statewise {
  background: royalblue !important;
  color: #fff;
}

.corona-wrapper .corona-items-heading * {
  font-weight: bold;
}

.corona-wrapper .corona-item:nth-child(2n) .corona-cases-citywise-list,
.corona-wrapper .corona-item:nth-child(odd) .corona-cases-statewise {
  background-color: #f7f7f7
}

.corona-wrapper .corona-item:nth-child(2n),
.corona-wrapper .corona-item:nth-child(odd) .corona-cases-citywise-list {
  background-color: #fff
}

.corona-wrapper .corona-item:nth-child(odd) .corona-cases-citywise-list .corona-cases-citywise,
.corona-wrapper .corona-item:nth-child(2n) .corona-cases-citywise-list .corona-cases-citywise {
  border-bottom: 3px solid #fff
}

.corona-wrapper .corona-item .corona-cases-citywise-list {
  display: none
}

.corona-wrapper .corona-item .corona-cases-citywise,
.corona-wrapper .corona-item .corona-cases-statewise {
  display: flex;
  overflow: hidden;
  align-items: center;
}

.corona-wrapper .corona-item .corona-cases-citywise *,
.corona-wrapper .corona-item .corona-cases-statewise * {
  width: 25%;
  padding: 10px 0 10px 10px
}

.corona-wrapper .corona-item .corona-cases-citywise .show-citiwise-list,
.corona-wrapper .corona-item .corona-cases-statewise .show-citiwise-list {
  cursor: pointer;
  position: absolute;
  width: 10px;
  left: -25px;
  top: 0
}

.corona-wrapper .corona-item .corona-cases-citywise .show-citiwise-list:before,
.corona-wrapper .corona-item .corona-cases-statewise .show-citiwise-list:before {
  transition: all .3s ease;
  content: "";
  border-color: inherit;
  border-width: 0 2px 2px 0;
  border-style: solid;
  display: inline-block;
  padding: 3px;
  transform: rotate(-45deg);
  margin-right: 5px
}

.corona-wrapper .corona-item.show-details .corona-cases-citywise-list {
  display: block;
  margin: 30px 15px;
  padding: 10px;
  text-align: center
}

.corona-wrapper .corona-item.show-details .show-citiwise-list:before {
  -ms-transform: rotate(45deg);
  transform: rotate(45deg)
}

.corona-wrapper .corona-item .corona-cases-statewise .new-cases {
  font-size: 14px;
  font-weight: 700;
  padding: 8px 25px 0 0;
  float: right
}

.corona-wrapper .corona-item .corona-cases-statewise .new-cases:before {
  content: "";
  border-color: inherit;
  border-width: 0 2px 2px 0;
  border-style: solid;
  display: inline-block;
  padding: 2px;
  -ms-transform: rotate(-135deg);
  transform: rotate(-135deg);
  margin-right: 5px
}

.corona-wrapper .corona-item .corona-cases-statewise .corona-cases-confirmed sub {
  color: #ff8c00
}

.corona-wrapper .corona-item .corona-cases-statewise .corona-cases-active sub {
  color: #dc143c
}

.corona-wrapper .corona-item .corona-cases-statewise .corona-cases-deceased sub {
  color: #a1a1a1
}

.corona-wrapper .corona-item .corona-cases-statewise .corona-cases-recovered sub {
  color: #28a745
}

footer {
  position: relative;
  bottom: 0;
  padding: 15px 20px;
  left: 0;
  z-index: 1;
  right: 0;
  overflow: hidden;
  border-top: 1px solid #333;
}

footer * {
  color: #000
}

.author a {
  float: left;
  text-decoration: none
}

footer a {
  float: right
}

@media screen and (max-width: 768px) {
  .corona-wrapper {
    margin: 145px auto 30px;
  }

  header {
    flex-direction: column;
    padding: 7px 15px 0px;
    text-align: center;
  }

  header * {
    float: none;
    margin: 0 0 10px;
    display: block;
  }

  .right-section {
    width: 100%;
  }

  header>a {
    margin: 0;
  }

  header .total-percenage-cases {
    display: flex;
    margin: 0;
    justify-content: space-between;
  }

  h1 {
    font-size: 25px;
    margin-bottom: 30px;
  }

  .corona-wrapper .corona-item {
    margin-left: 10px
  }

  .corona-wrapper .total-cases>div {
    width: 100%;
  }

  .corona-wrapper .total-cases p+span {
    display: none;
  }

  .corona-wrapper .total-cases p {
    min-width: 150px;
    margin: 10px 0 5px;
    font-size: 20px;
    padding: 10px 14px;
  }

  .corona-wrapper .total-cases p.active {
    padding: 10px 15px;
  }

  .corona-wrapper .total-cases p sub {
    float: right;
    margin: 2px 0 0 10px;
    position: static;
  }

  .corona-wrapper .total-cases p span {
    font-size: 15px;
    margin-bottom: 5px;
  }

  .corona-wrapper .total-cases {
    flex-direction: column;
    margin-bottom: 15px;
  }

  .corona-graph>div {
    margin-bottom: 20px;
  }

  .corona-graph {
    margin: 0 0 20px;
  }

  .corona-wrapper .corona-item .corona-cases-citywise *,
  .corona-wrapper .corona-item .corona-cases-statewise * {
    padding: 10px 5px;
    overflow: hidden;
    word-break: break-word;
    font-size: 14px;
  }

  .corona-wrapper .corona-item .corona-cases-statewise .show-citiwise-list {
    left: -20px;
    top: 50%;
    transform: translate(0, -50%);
  }

  .corona-wrapper .corona-item.show-details .show-citiwise-list {
    top: 10px;
    transform: translate(0, 0);
  }

  .corona-wrapper .corona-item .corona-cases-citywise * {
    padding: 10px 0px;
    font-size: 12px;
  }

  .corona-wrapper .corona-item.show-details .corona-cases-citywise-list {
    margin: 15px 0;
    padding: 0;
  }

  .corona-wrapper .corona-item .corona-cases-statewise .new-cases,
  .corona-wrapper .corona-item .corona-cases-citywise .corona-cases-confirmed .new-cases {
    float: none;
    display: block;
    width: auto
  }

  .corona-wrapper .total-cases .latest-update {
    top: -21px;
    font-size: 13px;
  }

  footer {
    padding: 15px 5%;
    width: auto;
  }
}

@media screen and (max-width:480px) {
  header * {
    font-size: 13px;
  }

  .corona-cases-confirmed {
    display: none;
  }

  header img {
    height: 35px;
  }

  .corona-wrapper {
    margin: 120px auto 30px;
  }
}